.spin-game {
  text-align: center;
  color: #fff;
  background-color: #001f3f;
  padding: 20px;
  border-radius: 10px;
  width: 400px;
  margin: auto;
  font-family: Arial, sans-serif;
}

.header {
  margin-bottom: 20px;
}

.spinner {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  height: 60px; /* Match the height of the spinning elements */
  overflow: hidden; /* Hide overflow */
}

.counter-container {
  display: flex;
  overflow: hidden;
}

.counter-char {
  background-color: #007bff;
  padding: 0; /* Remove padding to match the exact size */
  margin: 5px;
  border-radius: 10px; /* Slightly rounded corners */
  width: 96px; /* Adjust width if needed */
  height: 120px; /* Exact height to fit one character */
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px; /* Increase font size */
  box-sizing: border-box; /* Include padding in the height and width */
  line-height: 120px; /* Ensure single line height matches container height */
}

button {
  background-color: #007bff;
  border: none;
  padding: 10px 20px;
  color: white;
  font-size: 1em;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background-color: #0056b3;
}

.libertycarz {
  margin-top: 20px;
  font-size: 0.8em;
}

@media screen and (min-width: 1025px) and (max-width: 1536px) {
  .zoom_container {
    zoom: 0.8;
  }
}
